import $ from 'jquery';
class Base {
    /**
     * [initPlayList ]
     * @return {[type]} [description]
     */
    initPlayList(){
        this.play_list
        .set( 'r2', {
            bonus : 6,
            tip : '01~112, <em class="red">6</em>',
            name : ''
        } )
        .set( 'r3', {
            bonus : 19,
            tip : '01~113, <em class="red">19</em>',
            name : ''
        } )
        .set( 'r4', {
            bonus : 78,
            tip : '01~114, <em class="red">78</em>',
            name : ''
        } )
        .set( 'r5', {
            bonus : 540,
            tip : '01~115, <em class="red">540</em>',
            name : ''
        } )
        .set( 'r6', {
            bonus : 90,
            tip : '01~116, <em class="red">90</em>',
            name : ''
        } )
        .set( 'r7', {
            bonus : 26,
            tip : '01~117, <em class="red">26</em>',
            name : ''
        } )
        .set( 'r8', {
            bonus : 9,
            tip : '01~118, <em class="red">9</em>',
            name : ''
        } );
    }
    /**
     * [initNumber ]
     * @return {[type]} [description]
     */
    initNumber(){
        for( let i = 1; i < 12; i++ ){
            this.number.add( ( '' + i ).padStart( 2, '0' ) );
        }
    }
    /**
     * [setOmit ]
     * @param {Map} omit [ map ]
     */
    setOmit( omit ){
        this.omit.clear();
        for ( let [ index, item ] of omit.entries() ) {
            this.omit.set( index, item );
        }
    }

    /**
     * [setOpenCode ]
     * @param {[Set]} code [ set ]
     */
    setOpenCode( code ) {
        this.open_code.clear();
        for( let item of code.values() ) {
            this.open_code.add( item );
        }
        this.updateOpenCode && this.updateOpenCode.call( this, code );
    }

    updateOpenCode( code ){

        $( this.open_code_list_el ).children('.ico-ball3').each( function( index, item ){
            $( item ).text( code[ index ] ); // 
        } );
    }

    /**
     * [toggleCodeActive ]
     * @param  {Event} e [Event]
     * @return {[type]}   [description]
     */
    toggleCodeActive( e ){
        let $cur = $( e.currentTarget );  // 
        $cur.toggleClass('btn-boll-active');
        this.getCount();  // , 
    }
    /**
     * [changePlayNav ]
     * @param  {Event} e [Event]
     * @return {[type]}   [description]
     */
    changePlayNav( e ) {
        let $cur = $( e.currentTarget );  // 
        $cur.addClass( 'active' ).siblings().removeClass('active');
        // 
        this.cur_play = $cur.attr( 'desc' ).toLocaleLowerCase();
        // 
        $( '#zx_sm span' ).html( this.play_list.get( this.cur_play).tip );
        // 
        $( '.boll-list .btn-boll' ).removeClass( 'btn-boll-active' );
        this.getCount(); // , 
    }

    /**
     * [assistHandle , ]
     * @param  {Event}  e [Event]
     * @return {Boolean}   [description]
     */
    assistHandle( e ){
        e.preventDefault();
        let $cur = $( e.currentTarget );  // 
        let index = $cur.index();
        $( '.boll-list .btn-boll' ).removeClass( 'btn-boll-active' );
        // 
        if ( index === 0 ){
            $( '.boll-list .btn-boll' ).addClass( 'btn-boll-active' );
        }
        //  6-11
        if ( index === 1 ){
            $( '.boll-list .btn-boll' ).each( ( i, t ) => {
                //  6-11
                if ( t.textContent - 5 > 0 ){
                    $( t ).addClass( 'btn-boll-active' );
                }
            } );
        }
        //  1-5
        if ( index === 2 ){
            $( '.boll-list .btn-boll' ).each( ( i, t ) => {
                //  1-5
                if ( t.textContent - 6 < 0 ){
                    $( t ).addClass( 'btn-boll-active' );
                }
            } );
        }
        // 
        if ( index === 3 ){
            $( '.boll-list .btn-boll' ).each( ( i, t ) => {
                if ( t.textContent % 2 === 1 ){
                    $( t ).addClass( 'btn-boll-active' );
                }
            } );
        }
        // 
        if ( index === 4 ){
            $( '.boll-list .btn-boll' ).each( ( i, t ) => {
                if ( t.textContent % 2 === 0 ){
                    $( t ).addClass( 'btn-boll-active' );
                }
            } );
        }
        // , 
        this.getCount();
    }

    /**
     * [getName ]
     * @return {[type]} [description]
     */
    getName(){
        return this.name;
    }

    /**
     * [addCode ]
     */
    addCode() {
        // 
        let $active = $( '.boll-list .btn-boll-active' ).text().match(/\d{2}/g);
        // , 0
        let active = $active ? $active.length : 0;
        // 
        let count = this.computeCount( active, this.cur_play );
        if ( count ){
            this.addCodeItem( $active.join( ' ' ),
                this.cur_play,
                this.play_list.get(this.cur_play).name,
                count
            );
        }
    }

    /**
     * [addCodeItem ]
     * @param {String} code     []
     * @param {String} type     []
     * @param {String} typeName []
     * @param {Number} count    []
     */
    addCodeItem( code, type, typeName, count ){
        const template = `
            <li codes="${type}|${code}" bonus="${count * 2}" count="${count}">
                <div class="code">
                    <b>${typeName}${count > 1 ? '' : ''}</b>
                    <b class="em">${code}</b>
                    [${count},<em class="code-list-monery">${count * 2}</em>]
                </div>
            </li>
        `;
        $( this.cart_el ).append( template ); // 
        this.getTotal(); // 
    }

    /**
     * [getCount , ]
     * @return {[type]} [description]
     */
    getCount() {
        // 
        let active = $( '.boll-list .btn-boll-active' ).length;

        let count = this.computeCount( active, this.cur_play );  // 
        let range = this.computeBonus( active, this.cur_play ); // 

        let money = count * 2; // 
        // 
        let win1 = range[ 0 ] - money;  // , , 
        let win2 = range[ 1 ] - money;  // 
        let template;
        let c1 = ( win1 < 0 && win2 < 0 ) ? Math.abs( win1 ) : win1;
        let c2 = ( win1 < 0 && win2 < 0 ) ? Math.abs( win2 ) : win2;
        // 
        if ( count === 0 ){
            template = `
                <b class="red">${count}</b> ,
                 <b class="red">${count*2}</b> 
            `;

        // 
        }else if( range[ 0 ] === range[ 1 ] ){

            template = `
            <b>${count}</b>  <b>${count*2}</b> 
            <em>:
                <strong class="red">${range[ 0 ]}</strong> 
                ${win1 >= 0 ? '' : ''}
                <strong class="${win1 >= 0 ? 'red' : 'green'}">${Math.abs(win1)}</strong> 
            </em>
            `;
        }else{
            template = `
            <b>${count}</b>  <b>${count*2}</b> 
            <em>:
                <strong class="red">${range[ 0 ]}</strong> 
                <strong class="red">${range[ 1 ]}</strong> 
                ${ ( win1 < 0 && win2 < 0 ) ? '' : ''}
                <strong class="${win1 >= 0 ? 'red' : 'green'}">${c1} </strong> 
                <strong class="${win2 >= 0 ? 'red' : 'green'}">${c2} </strong> 
            </em>
            `;
        }
        $( '.sel_info' ).html( template );
    }

    /**
     * [getTotal ]
     * @return {[type]} [description]
     */
    getTotal(){
        let count = 0;
        $( '.codelist li' ).each( ( index, item ) => {
            count += $(item).attr( 'count' ) * 1; // 
        } );
        $( '#count' ).text( count );
        $( '#money' ).text( count * 2 );
    }

    /**
     * [getRandom ]
     * @param  {[type]} num [description]
     * @return {[type]}     [description]
     */
    getRandom( num ) {
        let arr = [], index;
        let number = Array.from( this.number );
        while( num-- ){
            index = Number.parseInt( Math.random()*number.length );
            arr.push( number[ index ] );
            number.splice( index, 1 );
        }
        return arr.join( ' ' );
    }

    /**
     * [getRandomCode  - ]
     * @param  {[type]} e [description]
     * @return {[type]}   [description]
     */
    getRandomCode( e ) {
        e.preventDefault();
        // 
        let num = e.currentTarget.getAttribute( 'count' );
        let play = this.cur_play.match( /\d+/g )[ 0 ];
        // 
        if ( num === '0' ){
            $( this.cart_el ).html( '' );
        }
        // 
        else{
            for( let i = 0; i < num; i++ ){
                this.addCodeItem(
                    this.getRandom( play ),
                    this.cur_play,
                    this.play_list.get( this.cur_play ).name,
                    1
                );
            }
        }
    }
}

export default Base;



