class Timer {
    countdown( end, update, handle ){
        const now = new Date().getTime();
        if ( now - end > 0 ){
            handle.call( this );
        }else{
            let last_time = end - now;  //  - 
            const px_d = 1000 * 60 * 60 * 24;  // 
            const px_h = 1000 * 60 * 60; // 
            const px_m = 1000 * 60; // 
            const px_s = 1000; // 
            let d = Math.floor( last_time / px_d );  // 

            const rest_h_ms = last_time - d * px_d;  // 
            let h = Math.floor( rest_h_ms / px_h );  // 

            const rest_m_ms = rest_h_ms -  h * px_h;  // 
            let m =Math.floor( rest_m_ms / px_m );  // 

            const rest_s_ms = rest_m_ms -  m * px_m;  // 
            let s =Math.floor( rest_s_ms / px_s );  // 

            let r = [];
            if ( d > 0 ){
                r.push( `<em>${d}</em>` );
            }
            if ( r.length || h > 0 ){
                r.push( `<em>${ String(h).padStart( 2, '0' ) }</em>` );
            }
            if ( r.length || m > 0 ){
                r.push( `<em>${ String(m).padStart( 2, '0' ) }</em>` );
            }
            if ( r.length || s > 0 ){
                r.push( `<em>${ String(s).padStart( 2, '0' ) }</em>` );
            }
            // console.log( r )
            this.last_time = r.join( '' );
            update.call( this, r.join( '' ) );
            setTimeout( () => {
                this.countdown( end, update, handle );
            }, 1000 );
        }
    }
}

export default Timer;



